.section {
	margin-bottom: 1rem;
    page-break-inside:avoid; 
    page-break-after:auto 
}

.cut {
	padding-bottom: .5rem;
}

@media screen {
	.cut {
	  	overflow: hidden;
	  	padding: 0 0 18px;
	  	margin-bottom: 1em;
	}

	.cut-bd {
	  	padding: .5em .5em 0;
	  	position: relative;
	}

	.cut-bd_red {
	  	background: #f90;
	  	color: #fff;
		padding-top: .5em;
  		border-top: 5px solid #fc0;
	}
	.cut-bd_red, .cut-bd_red > * {
	  	font-size: 18px;
	}
	.cut-bd_red:before {
		content: "";
		width: 100%;
		height: 24px;
		position: absolute;
		top: 100%;
		right: 24px;
		background: #f90;
	}
	.cut-bd_red:after {
		content: "";
		width: 0;
		height: 0;
		margin-left: -24px;
		position: absolute;
		top: 100%;
		left: 100%;
		border: 15px solid #f90;
		border-right: 15px solid transparent;
		border-bottom: 15px solid transparent;
	}
}

@media print {
	.cut-bd_red {
  		padding-bottom: .25rem;
  		border-bottom: 5px solid #fc0;
  		margin-bottom: .25rem;
	}
}


