.head {
	margin-bottom: 1rem;

	font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
	color: rgba(0,0,0,.87);
}

.head > h1 {
	margin: 0 !important;
}

.sub-header {
	text-align: center;
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.2em;
    color: rgba(0,0,0,.6);
}

@media print {
	.sub-header a {
	    color: rgba(0,0,0,.6);
	}
}


